let Transaction = require('../model/transaction')
let User = require('../model/user')

const state = {}

const mutations = {}

const actions = {
  listTransactions ({ commit }, { page, response }) {
    Transaction.list(page, res => {
      response(res)
    })
  },
  listOnlyNoReturnTransactions ({ commit }, { page, response }) {
    Transaction.onlyNoReturn(page, res => {
      response(res)
    })
  },
  listOnlyMyTransactions ({ rootState }, { page, response }) {
    if (rootState.user.userProfile === undefined) {
      User.getUserProfile(res => {
        if (res.error.code !== 0) {
          response({
            error: { code: 1, message: 'cannot get user profile' }
          })
        } else {
          Transaction.findMyBorrowerObjectId(page, res.data.objectId, res => {
            response(res)
          })
        }
      })
    } else {
      Transaction.findMyBorrowerObjectId(
        page,
        rootState.user.userProfile.objectId,
        res => {
          response(res)
        }
      )
    }
  },
  addTransaction ({ commit }, { transaction, response }) {
    Transaction.create(transaction, res => {
      response(res.error)
    })
  },
  returnTransaction ({ commit }, { transaction, response }) {
    console.log(transaction)
    transaction.returnAt = new Date().getTime()
    Transaction.update(transaction, res => {
      response(res.error)
    })
  },
  findTransactionByBorrower ({ commit }, { page, borrower, response }) {
    Transaction.findByBorrowerObjectId(page, borrower.objectId, res => {
      response(res)
    })
  },
  downloadTransaction ({ commit }, { response }) {
    Transaction.download(res => {
      response(res)
    })
  }
}

const getters = {}

module.exports = {
  state,
  mutations,
  actions,
  getters
}
