import axios from 'axios'

const searchUsers = async (keyword, callback) => {
  try {
    const result = await axios.get(process.env.VUE_APP_SERVICE_BASE_URL + '/user/search?keyword=' + keyword,
      {
        headers: {
          'access-token': window.localStorage.getItem('access-token')
        }
      })
    const json = result.data
    if (json.error.code === 0) {
      callback({
        error: { code: 0, message: 'success' },
        data: json.data
      })
    } else {
      callback({
        error: { code: 1, message: 'list user fail' },
        data: json.error
      })
    }
  } catch (error) {
    callback({
      error: { code: 1, message: 'list user fail' }
    })
  }
}

const list = async (page, callback) => {
  try {
    const result = await axios.get(process.env.VUE_APP_SERVICE_BASE_URL + '/user/all/?page=' + page + '&amount=10',
      {
        headers: {
          'access-token': window.localStorage.getItem('access-token')
        }
      })
    const json = result.data
    if (json.error.code === 0) {
      callback({
        error: { code: 0, message: 'success' },
        data: json.data,
        pagination: json.pagination
      })
    } else {
      callback({
        error: { code: 1, message: 'list user fail' },
        data: json.error
      })
    }
  } catch (error) {
    callback({
      error: { code: 1, message: 'list user fail' }
    })
  }
}

const create = async (user, callback) => {
  try {
    const result = await axios.post(process.env.VUE_APP_SERVICE_BASE_URL + '/user/add', user,
      {
        headers: {
          'access-token': window.localStorage.getItem('access-token')
        }
      })
    const json = result.data
    if (json.error.code === 0) {
      callback({
        error: { code: 0, message: 'success' }
      })
    } else {
      callback({
        error: { code: 1, message: 'create user fail' },
        data: json.error
      })
    }
  } catch (error) {
    callback({
      error: { code: 1, message: 'create user fail' }
    })
  }
}

