import axios from 'axios'

const findByBorrowerObjectId = async (page, borrowerObjectId, response) => {
  try {
    const result = await axios.get(
      process.env.VUE_APP_SERVICE_BASE_URL +
        '/transaction/borrower/' +
        borrowerObjectId +
        '/all?returned=false&page=' +
        page +
        '&amount=10',
      {
        headers: {
          'access-token': window.localStorage.getItem('access-token')
        }
      }
    )
    const json = result.data
    if (json.error.code === 0) {
      response({
        error: { code: 0, message: 'success' },
        data: json.data,
        pagination: json.pagination
      })
    } else {
      response({
        error: { code: 1, message: 'find transaction fail' },
        data: json.error
      })
    }
  } catch (error) {
    response({ error: { code: 1, message: 'find transaction fail' } })
  }
}
