let Image = require('../model/image')
const state = {
  picture: []
}

const mutations = {
  SET_PICTURES(state, { picture, page }) {
    let arrPicture = []
    if (state.picture.length > 0) {
      arrPicture = state.picture
    }
    arrPicture[page] = picture
    state.picture = arrPicture
  }
}

const actions = {
  addPicture({ commit }, picture) {
    commit('SET_PICTURES', picture)
  },
  uploadImage({ commit }, { file, page, response }) {
    Image.upload(file, res => {
      if (res.error.code === 0) {
        let picture = {
          numPicture: page,
          imagePath: res.data.imagePath,
          imageName: res.data.imageName
        }
        commit('SET_PICTURES', { picture, page })
      }
      response(res)
    })
  }
}

const getters = {
  picture(state) {
    return state.picture
  }
}

module.exports = {
  state,
  mutations,
  actions,
  getters
}
