<template>
<div class="card border-0">
  <div class="card-body">
    <nav aria-label="breadcrumb">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <router-link :to="{name:'listusers'}">จัดการผู้ใช้</router-link>
        </li>
        <li class="breadcrumb-item active" aria-current="page">แก้ไขผู้ใช้</li>
      </ol>
    </nav>
    <form v-if="isRequesting === false">
      <div v-if="errorMessage" class="alert alert-danger" role="alert">
        {{ errorMessage }}
      </div>
      <div v-if="successMessage" class="alert alert-success" role="alert">
        {{ successMessage }}
      </div>
      <div class="form-group">
        <label for="formGroupExampleInput">รหัสพนักงาน <add-star /></label>
        <input type="text" class="form-control" v-model="user.code">
      </div>
      <div class="form-group">
        <label for="formGroupExampleInput2">อีเมล <add-star /></label>
        <input type="text" class="form-control" v-model="user.email" @focus="clearErrorMessage">
      </div>
      <div class="form-group">
        <label for="formGroupExampleInput2">ชื่อ <add-star /></label>
        <input type="text" class="form-control" v-model="user.firstname" @focus="clearErrorMessage">
      </div>
      <div class="form-group">
        <label for="formGroupExampleInput2">นามสกุล<span style="color:red">*</span></label>
        <input type="text" class="form-control" v-model="user.lastname" @focus="clearErrorMessage">
      </div>
      <div class="form-group">
        <label for="formGroupExampleInput2">ชื่อเล่น</label>
        <input type="text" class="form-control" v-model="user.nickname" @focus="clearErrorMessage">
      </div>
      <div class="form-group">
        <label for="formGroupExampleInput2">แผนก</label>
        <input type="text" class="form-control" v-model="user.department">
      </div>
      <div v-if="user.email !== 'khomkrit@doitung.org'" class="form-group">
        <label for="formGroupExampleInput2">Role</label>
        <select class="form-control" v-model="user.role">
          <option value="normal">ผู้ใช้งานทั่วไป</option>
          <option value="admin">แอดมิน</option>
        </select>
      </div>
    </form>
    <div v-if="isRequesting === false" class="row">
      <div class="col d-flex justify-content-between align-items-center">
        <button class="btn btn-primary" @click="save">บันทึก</button>
        <button v-if="user.email !== 'khomkrit@doitung.org' && this.user.objectId !== ''" class="btn btn-danger" @click="deleteUser">ลบ</button>
      </div>
    </div>
    <app-loading v-if="isRequesting === true"></app-loading>
  </div>
</div>
</template>
<script>
import { mapActions } from 'vuex'
import Loading from '@/components/Loading'
import StarRed from '../../components/StarRed'
export default {
  data () {
    return {
      successMessage: '',
      errorMessage: '',
      user: {
        objectId: '',
        code: '',
        email: '',
        firstname: '',
        lastname: '',
        nickname: '',
        department: '',
        role: 'normal'
      },

      isRequesting: false
    }
  },
  components: {
    AppLoading: Loading,
    AddStar: StarRed
  },
  methods: {
    ...mapActions({
      findUserByObjectId: 'findUserByObjectId',
      addUser: 'addUser',
      updateUser: 'updateUser',
      userDeleteUser: 'deleteUser',
      clearUsers: 'clearUsers'
    }),
    clearErrorMessage () {
      this.errorMessage = ''
    },
    save () {
      this.successMessage = ''
      this.errorMessage = ''
      const vm = this
    console.log(this.user.code)
      if (!this.user.code || this.user.code.trim() === '' || this.user.email.trim() === '' || this.user.firstname.trim() === '' || this.user.lastname.trim() === '') {
        this.errorMessage = 'กรุณาระบุ รหัสพนักงาน อีเมล ชื่อ นามสกุล และชื่อเล่นให้ครบ'
        return
      }

      if (this.user.objectId === '') {
        this.isRequesting = true
        this.addUser({
          user: this.user,
          response (error) {
            vm.isRequesting = false
            if (error.code === 0) {
              vm.clearUsers()
              vm.successMessage = 'Save user success'
              return
            }
            vm.errorMessage = 'Save user error'
          }
        })
      } else {
        const vm = this
        this.isRequesting = true
        this.updateUser({
          user: vm.user,
          response: (error) => {
            vm.isRequesting = false
            if (error.code === 0) {
              vm.clearUsers()
              vm.successMessage = 'Update success'
              return
            }
            vm.errorMessage = 'Update fail'
          }
        })
      }
    },
    deleteUser () {
      const objectId = this.user.objectId
      if (confirm('คุณต้องการที่จะลบ ?')) {
        if (objectId !== undefined && objectId !== '') {
          const vm = this
          this.isRequesting = true
          this.userDeleteUser({
            objectId,
            response: (res) => {
              this.isRequesting = false
              if (res.error.code === 0) {
                vm.clearUsers()
                return this.$router.replace({ name: 'listusers' })
              }
              vm.errorMessage = 'Delete fail'
            }
          })
        }
      }
    },
    reload () {
      const objectId = this.$route.params.objectId

      if (objectId !== undefined && objectId !== '') {
        const vm = this
        this.isRequesting = true
        this.findUserByObjectId({
          objectId,
          response: (res) => {
            vm.isRequesting = false
            if (res.data === undefined) {
              return vm.$router.replace({ name: 'listusers' })
            }
            vm.user = res.data
          }
        })
      }
    }
  },
  mounted () {
    this.reload()
  }
}
</script>
