<template>
  <nav class="navbar navbar-expand-sm navbar-light bg-light">
    <a class="navbar-brand" href="#">Borrow</a>
    <button
      class="navbar-toggler"
      type="button"
      data-toggle="collapse"
      data-target="#navbarNavAltMarkup"
      aria-controls="navbarNavAltMarkup"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
      <div class="navbar-nav">
        <router-link :to="{name: 'borrow'}" class="nav-item nav-link">ยืม/คืน</router-link>
        <li v-if="isAdmin" class="nav-item dropdown">
          <a
            class="nav-link dropdown-toggle"
            href="#"
            id="navbarDropdownMenuLink"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >การจัดการระบบ</a>
          <div class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
            <router-link :to="{name: 'listthings'}" class="dropdown-item">จัดการสิ่งของที่ให้ยืมได้</router-link>
            <router-link :to="{name: 'listusers'}" class="dropdown-item">จัดการผู้ใช้</router-link>
          </div>
        </li>
        <li v-if="isAdmin" class="nav-item dropdown">
          <a
            class="nav-link dropdown-toggle"
            href="#"
            id="navbarDropdownMenuLink"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >รายการ</a>
          <div class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
            <router-link
              :to="{ name: 'listtransactions'}"
              class="dropdown-item"
            >รายการยืม/คืนทั้งหมด</router-link>
            <router-link
              :to="{ name: 'listNoReturnTransactions'}"
              class="dropdown-item"
            >รายการที่ยังไม่ได้รับคืน</router-link>
          </div>
        </li>
        <router-link :to="{name: 'myTransactions'}" class="nav-item nav-link">การยืมของฉัน</router-link>
        <router-link :to="{name: 'login'}" class="nav-item nav-link" v-if="!isLogin">Login</router-link>
        <a href="#" class="nav-item nav-link" @click="signOut" v-if="isLogin">
          Logout (
          <i class="fas fa-user"></i>
          {{ userProfile.firstname +' '+ userProfile.lastname}})
        </a>
      </div>
    </div>
  </nav>
</template>

<script>
import firebase from 'firebase/app'
import 'firebase/auth'
import { mapGetters, mapActions } from 'vuex'
export default {
  computed: {
    ...mapGetters({
      userProfile: 'userProfile',
      isAdmin: 'isAdmin'
    }),
    isLogin () {
      return this.userProfile !== undefined
    }
  },
  watch: {
    userProfile: function () {}
  },
  methods: {
    ...mapActions({
      logout: 'logout',
      clearUsers: 'clearUsers',
      clearThings: 'clearThings'
    }),
    async signOut () {
      if (confirm('คุณต้องการออกจากระบบ ?')) {
        await firebase.auth().signOut()
        this.logout()
        this.clearUsers()
        this.clearThings()
        this.$router.replace({ name: 'login' })
      }
    }
  }
}
</script>
