import axios from 'axios'

const list = async (page, type, response) => {
  let result
  try {
    if (page === undefined) {
      if (type === 'FOOD_BOX' || type === 'CLOTH_BAG') {
        result = await axios.get(
          process.env.VUE_APP_SERVICE_BASE_URL +
            '/thing/all?page=' +
            page +
            '&type=' +
            type +
            '&amount=10',
          {
            headers: {
              'access-token': window.localStorage.getItem('access-token')
            }
          }
        )
      } else {
        result = await axios.get(
          process.env.VUE_APP_SERVICE_BASE_URL + '/thing/all',
          {
            headers: {
              'access-token': window.localStorage.getItem('access-token')
            }
          }
        )
      }
    } else {
      if (type === 'FOOD_BOX' || type === 'CLOTH_BAG') {
        result = await axios.get(
          process.env.VUE_APP_SERVICE_BASE_URL +
            '/thing/all?page=' +
            page +
            '&type=' +
            type +
            '&amount=10',
          {
            headers: {
              'access-token': window.localStorage.getItem('access-token')
            }
          }
        )
      } else {
        if (type === 'FOOD_BOX' || type === 'CLOTH_BAG') {
          result = await axios.get(
            process.env.VUE_APP_SERVICE_BASE_URL +
              '/thing/all?page=' +
              page +
              '&type=' +
              type +
              '&amount=10',
            {
              headers: {
                'access-token': window.localStorage.getItem('access-token')
              }
            }
          )
        } else {
          result = await axios.get(
            process.env.VUE_APP_SERVICE_BASE_URL +
              '/thing/all?page=' +
              page +
              '&amount=10',
            {
              headers: {
                'access-token': window.localStorage.getItem('access-token')
              }
            }
          )
        }
      }
    }

    const json = result.data
    if (json.error.code !== 0) {
      response({
        error: { code: 1, message: 'list thing fail' }
      })
      return
    }

    const things = json.data
    const pagination = json.pagination

    response({
      error: { code: 0, message: 'success' },
      data: things,
      pagination: pagination
    })
  } catch (error) {
    response({
      error: { code: 1, message: 'list thing fail' }
    })
  }
}

const create = async (thing, response) => {
  try {
    const result = await axios.post(
      process.env.VUE_APP_SERVICE_BASE_URL + '/thing/create',
      thing,
      {
        method: 'POST',
        headers: {
          'access-token': window.localStorage.getItem('access-token')
        }
      }
    )
    const json = await result.data
    if (json.error.code !== 0) {
      response({
        error: { code: 1, message: 'create thing fail' }
      })
      return
    }

    response({
      error: { code: 0, message: 'success' },
      data: json.data
    })
  } catch (error) {
    response({
      error: { code: 1, message: 'update thing fail' }
    })
  }
}

