const findByObjectId = async (objectId, response) => {
  try {
    const result = await axios.get(
      process.env.VUE_APP_SERVICE_BASE_URL + '/thing/' + objectId + '/detail',
      {
        headers: {
          'access-token': window.localStorage.getItem('access-token')
        }
      }
    )
    const json = await result.data
    if (json.error.code !== 0) {
      response({
        error: { code: 1, message: 'get thing detail fail' }
      })
      return
    }

    response({
      error: { code: 0, message: 'success' },
      data: json.data
    })
  } catch (error) {
    response({ error: { code: 1, message: 'not found thing' } })
  }
}

const deleteThing = async (objectId, response) => {
  try {
    const result = await axios.post(
      process.env.VUE_APP_SERVICE_BASE_URL + '/thing/' + objectId + '/delete',
      {},
      {
        method: 'POST',
        headers: {
          'access-token': window.localStorage.getItem('access-token')
        }
      }
    )
    const json = await result.data
    if (json.error.code !== 0) {
      response({
        error: { code: 1, message: 'delete thing fail' }
      })
      return
    }

    response({ error: { code: 0, message: 'success' } })
  } catch (error) {
    response({ error: { code: 1, message: 'not found thing' } })
  }
}
const searchThing = async (keyword, response) => {
  try {
    const result = await axios.get(
      process.env.VUE_APP_SERVICE_BASE_URL + '/thing/search?keyword=' + keyword,
      {
        headers: {
          'access-token': window.localStorage.getItem('access-token')
        }
      }
    )
    const json = await result.data
    if (json.error.code !== 0) {
      response({
        error: { code: 1, message: 'create thing fail' }
      })
      return
    }

    response({
      error: { code: 0, message: 'success' },
      data: json.data
    })
  } catch (error) {
    response({
      error: { code: 1, message: 'update thing fail' }
    })
  }
}

export { list, create, deleteThing, findByObjectId, update, searchThing }
