<template>

  <div class="row justify-content-center">
    <div class="jumbotron mt-4">
      <h1 class="display-4">
        แบ่งกันใช้
        <i class="far fa-smile-wink"></i>
      </h1>
      <!-- <p
        class="lead"
      >วันนี้จะยืมอะไรดีนะ? ร่ม กล่องอาหาร สายชาร์จ รองเท้าแตะ หรือหนังสือน่าอ่านสักเล่ม</p> -->
      <p class="lead" id="cover"></p>
      <hr class="my-4">
      <p>
        ล็อคอินเข้าใช้งานด้วยอีเมลขององค์กรได้เลยจ้า
        <span style="color:red">example@doitung.org</span>
      </p>
      <div v-if="errorMessage" class="alert alert-info" role="alert">{{ errorMessage }}</div>
      <button
        v-if="isRequesting === false"
        class="btn btn-primary btn-lg"
        @click="signInWithGoogle"
      >
        <i class="fas fa-key"></i> เข้าใช้งาน
      </button>
      <button v-else class="btn btn-primary" type="button" disabled>
        <span class="spinner-grow spinner-grow-lg" role="status" aria-hidden="true"></span>
      </button>
    </div>
</div>

</template>
<script>
import firebase from 'firebase/app'
import 'firebase/auth'
import { mapActions, mapGetters } from 'vuex'
export default {
  data () {
    return {
      errorMessage: '',
      isRequesting: false,
      username: '',
      password: ''
    }
  },
  computed: {
    ...mapGetters({
      userProfile: 'userProfile'
    })
  },
  methods: {
    ...mapActions({
      saveCurrentUserProfile: 'saveCurrentUserProfile',
      getAccessToken: 'getAccessToken',
      getUserProfile: 'getUserProfile'
    }),
    async signInWithGoogle () {
      const vm = this

      this.isRequesting = true
      this.errorMessage = ''
      try {
        let provider = new firebase.auth.GoogleAuthProvider()
        const result = await firebase.auth().signInWithPopup(provider)
        if (result.additionalUserInfo.profile.hd !== 'doitung.org') {
          this.errorMessage =
            'ขออภัย อีเมล ' +
            result.additionalUserInfo.profile.email +
            ' ไม่ได้รับสิทธิ์เข้าใช้งาน กรุณาใช้ อีเมล@doitung.org'
          this.isRequesting = false
          return firebase.auth().signOut()
        }
        let idToken = await firebase.auth().currentUser.getIdToken(true)
        vm.getAccessToken({
          idToken: idToken,
          response: res => {
            if (res.error.code !== 0) {
              vm.isRequesting = false
              console.error(res)
              return
            }

            vm.getUserProfile(res => {
              vm.isRequesting = false
              if (res.error.code !== 0) {
                console.error(res)
                return
              }
              vm.saveCurrentUserProfile(res.data)
              vm.$router.replace({ name: 'borrow' })
            })
          }
        })
      } catch (error) {
        this.isRequesting = false
        this.errorMessage = error
      }
    }
  },
  mounted () {
    if (this.userProfile !== undefined) {
      this.$router.replace({ name: 'borrow' })
    }
  }
}
</script>
