let User = require('../model/user')
let Auth = require('../model/auth')

const state = {
  userProfile: undefined,
  users: [],
  pagination: []
}

const mutations = {
  SET_USERS (state, users) {
    state.users = users
  },
  SET_CURRENT_USER (state, userProfile) {
    state.userProfile = userProfile
  },
  SET_PAGINATION (state, page) {
    state.pagination = page
  }
}

const actions = {
  getAccessToken ({ commit }, { idToken, response }) {
    Auth.getAccessToken(idToken, res => {
      if (res.error.code === 0) {
        window.localStorage.setItem('access-token', res.data.token)
      }
      response(res)
    })
  },
  logout ({ commit }, payload) {
    commit('SET_CURRENT_USER', undefined)
    commit('SET_USERS', [])
    window.localStorage.removeItem('access-token')
  },

  saveCurrentUserProfile ({ commit }, userProfile) {
    commit('SET_CURRENT_USER', userProfile)
  },

  clearUsers ({ commit }, payload) {
    commit('SET_USERS', [])
  },

  listUsers ({ commit }, { page, response }) {
    User.list(page, res => {
      if (res.error.code === 0) {
        commit('SET_USERS', res.data)
        commit('SET_PAGINATION', res.pagination)
      }
      response(res.error)
    })
  },

  searchUsers ({ state, commit }, { keyword, response }) {
    User.searchUsers(keyword, res => {
      response(res.data)
    })
  },

  addUser ({ commit }, { user, response }) {
    User.create(user, res => {
      response(res.error)
    })
  },

  updateUser ({ commit }, { user, response }) {
    User.update(user, res => {
      response(res.error)
    })
  },

  findUserByObjectId ({ state, commit }, { objectId, response }) {
    User.findByObjectId(objectId, res => {
      response(res)
    })
  },

  deleteUser ({ commit }, { objectId, response }) {
    User.deleteUser(objectId, res => {
      response(res)
    })
  },

  getUserProfile ({ commit }, response) {
    User.getUserProfile(res => {
      response(res)
    })
  }
}

const getters = {
  users (state) {
    return state.users
  },
  userProfile (state) {
    return state.userProfile
  },
  isAdmin (state) {
    if (state.userProfile !== undefined) {
      return state.userProfile.role === 'admin'
    }

    return false
  },
  pagination (state) {
    return state.pagination
  }
}

module.exports = {
  state,
  mutations,
  actions,
  getters
}
