<template>
  <div class="card border-0">
    <div class="card-body" v-if="userProfile && userProfile.role">
      <h3>ยืม/คืน</h3>

      <div v-if="users.length > 0 && userProfile.role === 'admin'" class="card">
        <div class="card-header">ค้นหาพนักงานที่ต้องการยืมหรือคืน</div>
        <div class="card-body">
          <div v-if="selectedUser === undefined">
            <input
              type="text"
              class="form-control"
              @keyup="searchUsers"
              placeholder="ค้นจากชื่อเล่น ชื่อจริง นามสกุล รหัสพนักงาน"
              v-model="keyword"
            />
            <ul class="list-group">
              <li
                v-for="user in foundUsers"
                :key="user.objectId"
                class="list-group-item list-user-item"
                @click="selectUser(user)"
              >{{ user.code }} {{ user.firstname }} {{ user.lastname }} {{ user.nickname }} {{ user.email }}</li>
            </ul>
          </div>
          <div v-if="selectedUser " class="d-flex justify-content-between align-items-center">
            <div>
              <div>{{ selectedUser.code }} {{ selectedUser.firstname }} {{ selectedUser.lastname }} {{ selectedUser.nickname }} {{ selectedUser.email }}</div>

              <div v-if="isCheckingNotReturnedTransactions === false">
                <small v-if="notReturnedTransactions.length === 0">
                  <i class="fas fa-check-circle" style="color:green"></i> ไม่พบรายการที่ยังไม่ได้คืน
                </small>
              </div>
              <div v-else>
                <small>กำลังตรวจสอบสิ่งของที่ยังไม่ได้คืน...</small>
              </div>
            </div>
            <button class="btn btn-primary" @click="clearSelectedUser">หาพนักงานใหม่</button>
          </div>
        </div>
      </div>
      <div v-if="notReturnedTransactions.length > 0" class="card mt-4" style="border-color: red;">
        <h6
          class="card-header"
          style="color:red"
        >พบรายการยืมที่ยังไม่ได้คืน {{ notReturnedTransactions.length }} รายการ</h6>
        <ol class="list-group list-group-flush">
          <li
            v-for="(transaction, index) in notReturnedTransactions"
            :key="index"
            class="list-group-item d-flex justify-content-between align-items-center"
          >
            <div>
              ยืมเมื่อ {{ new Date(transaction.createdAt) |moment("DD MMM YYYY เวลา HH:mm:ss")}} )
              <div
                style="color:red"
              >** กรุณาคืนก่อน {{new Date(transaction.dueDate) | moment("DD MMM YYYY เวลา HH:mm:ss")}}</div>
              <ol>
                <li
                  v-for="(thing, index) in transaction.borrowingThings"
                  :key="index"
                >{{ thing.code }} {{ thing.typeName }}</li>
              </ol>
            </div>
            <button v-if="userProfile.role === 'admin'"
              @click="verityReturnTransaction(transaction)"
              type="button"
              class="btn btn-success"
              data-toggle="modal"
              data-target="#exampleModalScrollable"
            >คืน</button>
            <router-link tag="button" v-else :to="{path:'/return'}"
              type="button"
              class="btn btn-success"
            >คืน</router-link>
          </li>
        </ol>
      </div>
      <div
        v-if="isBorrowSuccess == true"
        class="alert alert-success mt-4"
        role="alert"
      >Save Borrow success</div>
      <div
        v-if="selectedUser && things.length > 0 && userProfile.role === 'admin' && notReturnedTransactions.length === 0"
        class="card mt-4"
      >
      </div>
      <div
        v-if="(notReturnedTransactions.length === 0 && userProfile.role==='normal')||(selectedUser && things.length > 0 && userProfile.role === 'admin' && notReturnedTransactions.length === 0)"
      >
        <div v-if="things.length > 0 " class="card mt-4">
          <h6 class="card-header">เลือกสิ่งที่ต้องการยืม</h6>
          <div class="card-body">
            <div class="row">
              <div class="col">
                <div class="form-row">
                  <div class="col">
                    <div class="col d-flex justify-content-between align-items-center mb-3">
                      <div class=" w-25 mr-3" style="white-space:pre">กล่องข้าว</div>
                      <select class="form-control" v-model="selectedFoodBox">
                        <option value="undefined" disabled>กรุณาเลือกสิ่งของที่ต้องการยืม</option>
                        <option
                          v-for="(things,index) in optionFoodBox"
                          :key="index"
                          :value="things"
                          :disabled="(things.quantity>0)?false:true"
                        >{{things.code}} {{things.typeName}} {{things.description}} ({{things.quantity}})</option>
                      </select>
                    </div>
                    <div class="col d-flex justify-content-between align-items-center">
                      <div class="w-25 mr-3 " style="white-space:pre">ถุงผ้า</div>
                      <select class="form-control" v-model="selectedClothBag">
                        <option value="undefined" disabled>กรุณาเลือกสิ่งของที่ต้องการยืม</option>
                        <option
                          v-for="(things,index) in optionCloatBag"
                          :key="index"
                          :value="things"
                          :disabled="(things.quantity)?false:true"
                        >{{things.code}} {{things.typeName}} {{things.description}} ({{things.quantity}})</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <ul v-if="toBorrowThings.length > 0" class="list-group mt-4">
              <li
                v-for="(toBorrowThing, index) in toBorrowThings"
                :key="index"
                class="list-group-item d-flex justify-content-between align-items-center"
              >
                <div class="col-sm-6">{{ toBorrowThing.code }} {{ toBorrowThing.typeName }} {{toBorrowThing.description}}</div>
                <div>
                  <!-- <span
                    class="badge badge-success badge-pill"
                    style="margin-right:20px"
                  >{{ toBorrowThing.quantity }}</span> -->
                  <button class="btn btn-danger" @click="removeFromSelectedThings(toBorrowThing.type,index)">ลบ</button>
                </div>
              </li>
            </ul>
          </div>
          <div class="row justify-content-center" v-if="toBorrowThings.length>0">
            <button
              v-if="!isSavingTransaction"
              class="btn btn-primary mt-4 mb-4"
              @click="saveTransaction"
            >บันทึกการยืม</button>
            <button v-else class="btn btn-primary mb-4 mt-4" type="button" disabled>
              <span class="spinner-grow spinner-grow-sm" role="status" aria-hidden="true"></span>
              กำลังบันทึกการยืม
            </button>
          </div>
        </div>
      </div>
      <div
        v-if="things.length === 0 && selectedUser && userProfile.role === 'admin'"
        class="alert alert-info mt-4"
        role="alert"
      >
        <router-link
          :to="{ name: 'creatething' }"
          class="btn btn-primary"
          style="margin-right:20px"
        >เพิ่มสิ่งของที่ให้ยืมได้</router-link>
        <span>
          <i class="fas fa-info-circle"></i> ไม่มีสิ่งของที่สามารถให้ยืมได้ตอนนี้ กรุณาเพิ่มสิ่งของที่ให้ยืมได้ก่อน
        </span>
      </div>
      <div
        v-if="things.length === 0 && selectedUser && userProfile.role === 'normal'"
        class="alert alert-info mt-4"
        role="alert"
      >
        <span>
          <i class="fas fa-info-circle"></i> ไม่มีสิ่งของที่สามารถให้ยืมได้ตอนนี้ กรุณาเพิ่มสิ่งของที่ให้ยืมได้ก่อน
        </span>
      </div>

      <!-- Modal -->
      <div
        v-if="willReturnTransaction"
        class="modal fade"
        id="exampleModalScrollable"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalScrollableTitle"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-dialog-centered" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5
                class="modal-title"
                id="exampleModalScrollableTitle"
              >สิ่งของที่ {{ this.selectedUser.firstname }} ({{ this.selectedUser.nickname }}) นำมาคืนมี ดังนี้</h5>
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
                @click="willReturnTransaction = undefined"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              ยืมเมื่อ {{ new Date(willReturnTransaction.createdAt) | moment("DD, MMM YYYY เวลา HH:mm:ss")}}
              <ol style="padding-right:40px">
                <li v-for="(thing, index) in willReturnTransaction.borrowingThings" :key="index">
                  {{ thing.code }} {{ thing.typeName }}
                  <label>สถานะสิ่งของที่รับคืน</label>
                  <select class="form-control" v-model="returnStatus[index]">
                    <option value="normal">ปกติ</option>
                    <option value="lost">สูญหาย</option>
                    <option value="broken">ชำรุด</option>
                  </select>
                </li>
              </ol>
              <label class="mt-3">บันทึกอื่นๆ</label>
              <input type="text" class="form-control" v-model="returnDescription" />
              <label class="mt-3">รับคืนโดย (You)</label>
            </div>
            <div class="modal-footer">
              <span style="color:red;margin-right:20px">เมื่อยืนยันแล้วจะไม่สามารถแก้ไขได้</span>
              <button
                type="button"
                class="btn btn-primary"
                data-dismiss="modal"
                @click="confirmReturnTransaction"
              >ยืนยันการรับคืน</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import VueBootstrapTypeahead from 'vue-bootstrap-typeahead'
export default {
  data () {
    return {
      isBorrowSuccess: false,
      keyword: '',
      foundUsers: [],
      foundThings: [],
      optionListThings: [],
      selectedUser: undefined,
      selectedThing: undefined,

      selectedFoodBox: undefined,
      selectedClothBag: undefined,

      toBorrowQuantity: 1,
      toBorrowThings: [],

      notReturnedTransactions: [],
      isCheckingNotReturnedTransactions: false,

      willReturnTransaction: undefined,
      returnStatus: ['A', 'A'],
      returnDescription: '',
      isSavingTransaction: false,
      foundDuplicateThing: false,
      optionFoodBox: [],
      optionCloatBag: []
    }
  },
  computed: {
    ...mapGetters({
      things: 'things',
      users: 'users',
      userProfile: 'userProfile',
      foundThingsList: 'foundThingsList'
    })
  },
  methods: {
    ...mapActions({
      userSearchUsers: 'searchUsers',
      searchThings: 'searchThings',
      transactionAddTransaction: 'addTransaction',
      listUsers: 'listUsers',
      listThings: 'listThings',
      findTransactionByBorrower: 'findTransactionByBorrower',
      returnTransaction: 'returnTransaction'
    }),
    validateQuantityNumber () {
      if (isNaN(this.toBorrowQuantity)) {
        this.toBorrowQuantity = 1
      }
    },
    verityReturnTransaction (transaction) {
      this.willReturnTransaction = transaction
    },
    confirmReturnTransaction () {
      let borrowingThings = []
      this.willReturnTransaction.returnDescription = this.returnDescription
      this.willReturnTransaction.borrowingThings.forEach((element, i) => {
        borrowingThings.push({
          objectId: element.objectId,
          status: this.returnStatus[i]
        })
      })
      this.willReturnTransaction.borrowingThings = borrowingThings
      const vm = this
      this.returnTransaction({
        transaction: this.willReturnTransaction,
        response: error => {
          if (error.code !== 0) {
            return console.log('return fail')
          }

          vm.notReturnedTransactions = []
          vm.isCheckingNotReturnedTransactions = true
          vm.delayMessageSuccess()
          vm.findTransactionByBorrower({
            page: 1,
            borrower: vm.selectedUser,
            response: res => {
              vm.isCheckingNotReturnedTransactions = false
              if (res.error.code !== 0) {
                return console.log('fetch data error')
              }
              vm.notReturnedTransactions = res.data
            }
          })
        }
      })
    },
    searchUsers () {
      if (this.keyword.trim() === '') {
        return (this.foundUsers = [])
      }
      const vm = this
      this.userSearchUsers({
        keyword: vm.keyword,
        response: users => {
          vm.foundUsers = users
        }
      })
    },
    searchThingsFunc () {
      if (this.keywordThing.trim() === '') {
        return (this.foundThings = [])
      }
      const vm = this
      this.searchThings({
        keyword: vm.keywordThing,
        response: things => {
          vm.foundThings = things.error
          vm.optionListThings = things.data
        }
      })
    },
    delayMessageSuccess () {
      this.isBorrowSuccess = true
      setTimeout(() => {
        console.log((this.isBorrowSuccess = false))
      }, 5000)
    },
    selectThing () {
      this.foundThings = []
      this.addToSelectedThings()
    },
    selectUser (user) {
      this.selectedUser = user
      this.foundUsers = []

      const vm = this

      this.isCheckingNotReturnedTransactions = true
      this.findTransactionByBorrower({
        borrower: this.selectedUser,
        response: res => {
          this.isCheckingNotReturnedTransactions = false
          if (res.error.code !== 0) {
            return console.log('fetch data error')
          }
          vm.notReturnedTransactions = res.data
        }
      })
    },
    clearSelectedUser () {
      this.selectedUser = undefined
      this.selectedThing = undefined
      this.toBorrowThings = []
      this.toBorrowQuantity = 1
      this.notReturnedTransactions = []
      this.willReturnTransaction = undefined
    },
    addToSelectedThings () {
      if (this.notReturnedTransactions.length === 0) {
        if (this.selectedThing === undefined) {
          return
        }

        this.foundDuplicateThing = false
        for (let i = 0; i < this.toBorrowThings.length; i++) {
          if (this.toBorrowThings[i].type === this.selectedThing.type) {
            this.toBorrowThings.splice(i, 1, this.selectedThing)
            this.foundDuplicateThing = true
            break
          }
        }

        if (!this.foundDuplicateThing) {
          this.selectedThing.quantity = this.toBorrowQuantity
          this.toBorrowThings.push(this.selectedThing)
        }

        this.selectedThing = undefined
        this.toBorrowQuantity = 1
      }
      if (this.selectedThing === undefined) {
        return
      }

      this.foundDuplicateThing = false
      for (let i = 0; i < this.toBorrowThings.length; i++) {
        if (this.toBorrowThings[i].type === this.selectedThing.type) {
          this.foundDuplicateThing = true
          break
        }
      }

      if (!this.foundDuplicateThing) {
        this.selectedThing.quantity = this.toBorrowQuantity
        this.toBorrowThings.push(this.selectedThing)
      }

      this.selectedThing = undefined
      this.toBorrowQuantity = 1
      this.foundThingsList = []
    },
    removeFromSelectedThings (type, index) {
      if (type === 'FOOD_BOX') {
        this.selectedFoodBox = undefined
      } else {
        this.selectedClothBag = undefined
      }
      this.toBorrowThings.splice(index, 1)
    },
    saveTransaction () {
      if (this.toBorrowThings.length === 0) {
        return
      }
      const transaction = {
        borrowerId:
          this.userProfile.role === 'admin'
            ? this.selectedUser.objectId
            : this.userProfile.objectId,
        borrowingThings: this.toBorrowThings
      }

      const vm = this
      this.isSavingTransaction = true
      this.transactionAddTransaction({
        transaction: transaction,
        response: error => {
          this.isSavingTransaction = false
          if (error.code !== 0) {
            return console.log('save transaction fail')
          }
          this.selectedThing = undefined
          this.toBorrowThings = []
          this.toBorrowQuantity = 1
          vm.delayMessageSuccess()
          this.isCheckingNotReturnedTransactions = true
          vm.findTransactionByBorrower({
            borrower: vm.selectedUser,
            response: res => {
              vm.isCheckingNotReturnedTransactions = false
              if (res.error.code !== 0) {
                return console.log('fetch data error')
              }
              vm.notReturnedTransactions = res.data
            }
          })
        }
      })
    }
  },
  watch: {
    keywordThing () {
      this.searchThingsFunc()
    },
    selectedClothBag () {
      this.selectedThing = this.selectedClothBag
      this.selectThing()
    },
    selectedFoodBox () {
      this.selectedThing = this.selectedFoodBox
      this.selectThing()
    },
    userProfile () {
      if (this.userProfile.role === 'normal') {
        this.selectUser(this.userProfile)
      }
    }
  },
  mounted () {
    this.listUsers({
      page: 1,
      response: error => {
        if (error.code !== 0) {
          console.log('list users fail')
        }
      }
    })

    this.listThings({
      type: 'FOOD_BOX',
      response: res => {
        if (res.error.code !== 0) {
          console.log('list things fail')
        } else {
          this.optionFoodBox = res.data
        }
      }
    })

    this.listThings({
      type: 'CLOTH_BAG',
      response: res => {
        if (res.error.code !== 0) {
          console.log('list things fail')
        } else {
          this.optionCloatBag = res.data
        }
      }
    }),
    this.$nextTick(() => {
      if (this.userProfile) {
        this.selectUser(this.userProfile)
      }
    })
  },
  components: {
    VueBootstrapTypeahead
  }
}
</script>
<style>
.list-user-item:hover {
  background-color: #eeeeee;
  cursor: pointer;
}
</style>
