<template>
  <div class="card">
      <!-- <img src="http://dev1.mflf.io/resources/borrow/images/1620191562568911667" /> -->
      <div class="alert alert-info" role="alert" v-if="isErrorBeforeConfirmReturnTransaction">{{this.confirmReturnTransationError}}</div>
    <div v-for="(willReturnTransaction,index) in notReturnedTransactions" :key="index">
      <div v-if="selectedUser && selectedUser.firstname">
        <div>
          <div class="card-header">
            <h5>สิ่งของที่ {{ selectedUser.firstname }} ({{ selectedUser.nickname }}) นำมาคืนมี ดังนี้</h5>
            <!-- <button @click="willReturnTransaction = undefined"
              >
                <span aria-hidden="true">&times;</span>
            </button>-->
          </div>
          <div class="card-body">
            ยืมเมื่อ {{ new Date(willReturnTransaction.createdAt) | moment("DD, MMM YYYY เวลา HH:mm:ss")}}
            <ol style="padding-right:40px">
              <li v-for="(thing, index) in willReturnTransaction.borrowingThings" :key="index">
                {{ thing.code }} {{ thing.typeName }}
                <div class="row">
                  <div class="col-lg-3 col-sm-12 pb-2">
                    <label>สถานะสิ่งของที่รับคืน</label>
                  </div>
                  <div class="col-lg-5 col-sm-12 pb-2">
                    <select class="form-control form-control-sm" v-model="returnStatus[index]">
                      <option value="normal">ปกติ</option>
                      <option value="lost">สูญหาย</option>
                      <option value="broken">ชำรุด</option>
                    </select>
                  </div>
                  <div class="col-lg-4 col-sm-12 pb-2">
                    <router-link tag="button" :to="{path:'/takepicture/'+index}">
                      <i class="fas fa-camera"></i>
                    </router-link>
                  </div>
                </div>
                <div
                  class="col-lg-5 col-sm-12 pb-2 text-center"
                  v-if="pictureForValidate.length > 0 &&pictureForValidate[index] !== undefined"
                >
                  <!-- <button
                      type="button"
                      class="btn btn-success"
                      data-toggle="modal"
                      data-target="#exampleModalScrollable"
                    >
                      <i class="fas fa-camera"></i>
                  </button>-->
                  <!-- <div class="col-lg-5 col-sm-12 pb-2" v-if="pictureForValidate.length > 0 &&pictureForValidate[index] !== undefined"> -->
                  <img :src="pictureForValidate[index].imagePath" style="width:100px;height:100px;" />
                  <!-- </div> -->
                </div>
              </li>
            </ol>
            <label class="mt-3">บันทึกอื่นๆ</label>
            <input type="text" class="form-control" v-model="returnDescription" />
            <label class="mt-3">รับคืนโดย (You)</label>
            <span style="color:red;margin-right:20px">เมื่อยืนยันแล้วจะไม่สามารถแก้ไขได้</span>
            <button
              type="button"
              class="btn btn-primary"
              data-dismiss="modal"
              @click="confirmReturnTransaction"
            >ยืนยันการรับคืน</button>
          </div>
          <!-- <div class="card-footer">
              <span style="color:red;margin-right:20px">เมื่อยืนยันแล้วจะไม่สามารถแก้ไขได้</span>
              <button
                type="button"
                class="btn btn-primary"
                data-dismiss="modal"
                @click="confirmReturnTransaction"
              >ยืนยันการรับคืน</button>
          </div>-->
          <div
            v-if="willReturnTransaction"
            class="modal fade"
            id="exampleModalScrollable"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalScrollableTitle"
            aria-hidden="true"
          >
            <div class="modal-dialog modal-dialog-centered" role="document">
              <div class="modal-content">
                <div class="modal-header">
                  <h5 class="modal-title" id="exampleModalScrollableTitle">ถ่ายภาพของที่จะคืน</h5>
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                    @click="willReturnTransaction = undefined"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div class="modal-body">
                  <take-picture />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- </div> -->
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import VueBootstrapTypeahead from 'vue-bootstrap-typeahead'
import TakePicture from '../image/TakePicture'
export default {
  data () {
    return {
      isBorrowSuccess: false,
      keyword: '',
      foundUsers: [],
      foundThings: [],
      optionListThings: [],
      selectedUser: undefined,
      selectedThing: undefined,

      selectedFoodBox: undefined,
      selectedClothBag: undefined,

      toBorrowQuantity: 1,
      toBorrowThings: [],

      notReturnedTransactions: [],
      isCheckingNotReturnedTransactions: false,

      willReturnTransaction: undefined,
      returnStatus: ['normal', 'normal'],
      returnDescription: '',
      isSavingTransaction: false,
      foundDuplicateThing: false,
      optionFoodBox: [],
      optionCloatBag: [],
      pictureForValidate: [],
      confirmReturnTransationError: '',
      isErrorBeforeConfirmReturnTransaction: false
    }
  },
  computed: {
    ...mapGetters({
      things: 'things',
      users: 'users',
      userProfile: 'userProfile',
      foundThingsList: 'foundThingsList',
      picture: 'picture'
    })
  },
  methods: {
    ...mapActions({
      userSearchUsers: 'searchUsers',
      searchThings: 'searchThings',
      transactionAddTransaction: 'addTransaction',
      listUsers: 'listUsers',
      listThings: 'listThings',
      findTransactionByBorrower: 'findTransactionByBorrower',
      returnTransaction: 'returnTransaction'
    }),
    validateQuantityNumber () {
      if (isNaN(this.toBorrowQuantity)) {
        this.toBorrowQuantity = 1
      }
    },
    verityReturnTransaction (transaction) {
      this.willReturnTransaction = transaction
    },
    confirmReturnTransaction () {
      this.notReturnedTransactions.forEach(willReturnTransaction => {
        let borrowingThings = []
        if (
          willReturnTransaction.borrowingThings.length >
        this.pictureForValidate.length
        ) {
          this.confirmReturnTransationError = 'กรุณาอัพโหลดรูปให้ครบก่อนส่งคืน'
          this.isErrorBeforeConfirmReturnTransaction = true
        } else {
          this.isErrorBeforeConfirmReturnTransaction = false
          willReturnTransaction.returnDescription = this.returnDescription
          willReturnTransaction.borrowingThings.forEach((element, i) => {
            borrowingThings.push({
              objectId: element.objectId,
              status: this.returnStatus[i],
              imageNames: [this.pictureForValidate[i].imageName]
            })
          })
          willReturnTransaction.borrowingThings = borrowingThings
          const vm = this
          this.returnTransaction({
            transaction: willReturnTransaction,
            response: error => {
              if (error.code !== 0) {
                return console.log('return fail')
              } else {
                // this.$router.push({ path: '/' })
                window.location.replace('/')
              }

            // vm.notReturnedTransactions = [];
            // vm.isCheckingNotReturnedTransactions = true;
            // vm.delayMessageSuccess();
            // vm.findTransactionByBorrower({
            //   page: 1,
            //   borrower: vm.selectedUser,
            //   response: res => {
            //     vm.isCheckingNotReturnedTransactions = false;
            //     if (res.error.code !== 0) {
            //       return console.log("fetch data error");
            //     }
            //     vm.notReturnedTransactions = res.data;
            //   }
            // });
            }
          })
        }
      })
    },
    searchUsers () {
      if (this.keyword.trim() === '') {
        return (this.foundUsers = [])
      }
      const vm = this
      this.userSearchUsers({
        keyword: vm.keyword,
        response: users => {
          vm.foundUsers = users
        }
      })
    },
    searchThingsFunc () {
      if (this.keywordThing.trim() === '') {
        return (this.foundThings = [])
      }
      const vm = this
      this.searchThings({
        keyword: vm.keywordThing,
        response: things => {
          vm.foundThings = things.error
          vm.optionListThings = things.data
        }
      })
    },
    delayMessageSuccess () {
      this.isBorrowSuccess = true
      setTimeout(() => {
        console.log((this.isBorrowSuccess = false))
      }, 5000)
    },
    selectThing () {
      this.foundThings = []
      this.addToSelectedThings()
    },
    selectUser (user) {
      this.selectedUser = user
      this.foundUsers = []
      const vm = this
      this.isCheckingNotReturnedTransactions = true
      if (this.selectedUser != undefined) {
        this.findTransactionByBorrower({
          borrower: this.selectedUser,
          response: res => {
            this.isCheckingNotReturnedTransactions = false
            if (res.error.code !== 0) {
              return console.log('fetch data error')
            }
            vm.notReturnedTransactions = res.data
          }
        })
      }
    },
    clearSelectedUser () {
      this.selectedUser = undefined
      this.selectedThing = undefined
      this.toBorrowThings = []
      this.toBorrowQuantity = 1
      this.notReturnedTransactions = []
      this.willReturnTransaction = undefined
    },
    addToSelectedThings () {
      if (this.notReturnedTransactions.length === 0) {
        if (this.selectedThing === undefined) {
          return
        }

        this.foundDuplicateThing = false
        for (let i = 0; i < this.toBorrowThings.length; i++) {
          if (this.toBorrowThings[i].type === this.selectedThing.type) {
            this.toBorrowThings.splice(i, 1, this.selectedThing)
            this.foundDuplicateThing = true
            break
          }
        }

        if (!this.foundDuplicateThing) {
          this.selectedThing.quantity = this.toBorrowQuantity
          this.toBorrowThings.push(this.selectedThing)
        }

        this.selectedThing = undefined
        this.toBorrowQuantity = 1
      }
      if (this.selectedThing === undefined) {
        return
      }

      this.foundDuplicateThing = false
      for (let i = 0; i < this.toBorrowThings.length; i++) {
        if (this.toBorrowThings[i].type === this.selectedThing.type) {
          this.foundDuplicateThing = true
          break
        }
      }

      if (!this.foundDuplicateThing) {
        this.selectedThing.quantity = this.toBorrowQuantity
        this.toBorrowThings.push(this.selectedThing)
      }

      this.selectedThing = undefined
      this.toBorrowQuantity = 1
      this.foundThingsList = []
    },
    removeFromSelectedThings (type, index) {
      if (type === 'FOOD_BOX') {
        this.selectedFoodBox = undefined
      } else {
        this.selectedClothBag = undefined
      }
      this.toBorrowThings.splice(index, 1)
    }
    // saveTransaction () {
    //   if (this.toBorrowThings.length === 0) {
    //     return
    //   }
    //   const transaction = {
    //     borrowerId:
    //       this.userProfile.role === 'admin'
    //         ? this.selectedUser.objectId
    //         : this.userProfile.objectId,
    //     borrowingThings: this.toBorrowThings
    //   }

    //   const vm = this
    //   this.isSavingTransaction = true
    //   this.transactionAddTransaction({
    //     transaction: transaction,
    //     response: error => {
    //       this.isSavingTransaction = false
    //       if (error.code !== 0) {
    //         return console.log('save transaction fail')
    //       }
    //       this.selectedThing = undefined
    //       this.toBorrowThings = []
    //       this.toBorrowQuantity = 1
    //       vm.delayMessageSuccess()
    //       this.isCheckingNotReturnedTransactions = true
    //       vm.findTransactionByBorrower({
    //         borrower: vm.selectedUser,
    //         response: res => {
    //           vm.isCheckingNotReturnedTransactions = false
    //           if (res.error.code !== 0) {
    //             return console.log('fetch data error')
    //           }
    //           vm.notReturnedTransactions = res.data
    //         }
    //       })
    //     }
    //   })
    // }
  },
  watch: {
    picture () {
      if (this.picture) {
        this.pictureForValidate[this.picture.numPicture] = this.picture
      }
    },
    selectedClothBag () {
      this.selectedThing = this.selectedClothBag
      this.selectThing()
    },
    selectedFoodBox () {
      this.selectedThing = this.selectedFoodBox
      this.selectThing()
    }
  },
  watch: {
    userProfile () {
      let vm = this
      if (this.userProfile && this.userProfile.role === 'normal') {
        vm.selectUser(this.userProfile)
      }
    }
  },
  mounted () {
    this.listUsers({
      page: 1,
      response: error => {
        if (error.code !== 0) {
          console.log('list users fail')
        }
      }
    })

    this.listThings({
      type: 'FOOD_BOX',
      response: res => {
        if (res.error.code !== 0) {
          console.log('list things fail')
        } else {
          this.optionFoodBox = res.data
        }
      }
    })

    this.listThings({
      type: 'CLOTH_BAG',
      response: res => {
        if (res.error.code !== 0) {
          console.log('list things fail')
        } else {
          this.optionCloatBag = res.data
        }
      }
    })
    this.pictureForValidate = this.picture
    this.$nextTick(() => {
      if (this.userProfile && this.userProfile.role === 'normal') {
        this.selectUser(this.userProfile)
      }
    })
  },
  components: {
    VueBootstrapTypeahead,
    TakePicture
  }
}
</script>
<style>
.list-user-item:hover {
  background-color: #eeeeee;
  cursor: pointer;
}
</style>
