<template>
  <div class="container-fullwidth main">
    <nav-bar v-if="!shouldHideNavBar"></nav-bar>
    <div class="row justify-content-center mt-4">
      <div class="col-md-10 col-lg-10">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions } from 'vuex'
import NavBar from '@/components/NavBar'
export default {

  methods: {
    ...mapActions({
      getUserProfile: 'getUserProfile',
      saveCurrentUserProfile: 'saveCurrentUserProfile'
    }),
    configureBackgroundImage () {
      if (this.$route.name === 'login') {
        let body = document.body
        body.classList.add('login-bg')
      } else {
        let body = document.body
        body.classList.remove('login-bg')
      }
    }
  },
  watch: {
    $route () {
      this.configureBackgroundImage()
    }
  },
  computed: {
    shouldHideNavBar () {
      return this.$route.meta.shouldHideNavBar
    }
  },
  mounted () {
    this.configureBackgroundImage()

    this.getUserProfile(res => {
      if (res.error.code !== 0) {
        this.$router.push({ name: 'login' })
      } else {
        this.saveCurrentUserProfile(res.data)
      }
    })
  },
  components: {
    NavBar
  }
}
</script>
<style>
  body.login-bg {
    background-image: url('../public/bg-1.jpg');
  }
  .jumbotron{
      background-size: cover;
  }
  .jumbotron h1{
      margin: 10px;
      padding: 10px
  }
   .jumbotron p{
        margin: 10px;
      padding: 10px
  }
   .jumbotron button{
      margin: 10px
  }
</style>
