import axios from 'axios'

const upload = async (file, response) => {
  try {
    console.log(process.env.VUE_APP_SERVICE_BASE_URL + '/upload/image')
    const result = await axios.post(
      process.env.VUE_APP_SERVICE_BASE_URL + '/upload/image',
      file,
      {
        headers: {
          'access-token': window.localStorage.getItem('access-token')
        }
      }
    )
    const json = result.data
    if (json.error.code === 0) {
      response({
        error: { code: 0, message: 'success' },
        data: json.data
      })
    } else {
      response({
        error: { code: 1, message: 'upload image fail' },
        data: json.error
      })
    }
  } catch (error) {
    // response({ error: { code: 1, message: 'find transaction fail' } })
    console.log(error)
  }
}

export { upload }
