<template>
<div class="card border-0">
  <div class="card-body">
    <h3>จัดการผู้ใช้</h3>
    <div class="alert alert-info" role="alert">
      <router-link
        :to="{ name: 'createuser', params: { objectId: '' } }"
        class="btn btn-primary"
        style="margin-right:20px"
      >เพิ่มผู้ใช้</router-link>
      <span>
        <i class="fas fa-info-circle"></i> ผู้ใช้ที่ถูกเพิ่มเข้ามาในระบบแล้วเท่านั้นที่สามารถยืมของได้
      </span>
    </div>
    <form @submit.prevent="searchUsers" class="mt-4">
      <div class="row">
        <div class="col d-flex justify-content-between align-items-center">
          <input
            type="text"
            class="form-control"
            placeholder="ค้นหาผู้ใช้จาก รหัส ชื่อ ชื่อเล่น แผนก (จำกัดไว้ที่ 5 ผลลัพธ์)"
            style="margin-right: 20px"
            v-model="keyword"
            @keyup="validateKeyword"
          >
          <button class="btn btn-primary">ค้นหา</button>
        </div>
      </div>
    </form>
    <table v-if="isRequesting === false" class="table mt-4 table-responsive-lg">
      <thead class="thead-dark">
        <tr>
          <th scope="col">ลำดับ</th>
          <th scope="col">รหัสพนักงาน</th>
          <th scope="col">ชื่อ - สกุล</th>
          <th scope="col">อีเมล</th>
          <th scope="col">แผนก</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(user, index) in displayUsers"
          :key="user.objectId"
          @click="showUserDetail(user.objectId)"
        >
          <th scope="row">{{ setOrder(index) }}</th>
          <td>{{ user.code }}</td>
          <td>
            {{ user.firstname }} {{ user.lastname }} ({{ user.nickname }})
            <i
              class="fas fa-user-tie"
              v-if="user.role === 'admin'"
            ></i>
          </td>
          <td>{{ user.email }}</td>
          <td>{{ user.department }}</td>
        </tr>
      </tbody>
    </table>
    <app-loading v-if="isRequesting === true"></app-loading>
    <v-pagination v-show="this.isShowPagination"
      class="float-right"
      v-model="currentPage"
      :page-count="totalPages"
      :classes="bootstrapPaginationClasses "
      :labels="paginationAnchorTexts"
    ></v-pagination>
  </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import Loading from '@/components/Loading'
import vPagination from 'vue-plain-pagination'
export default {
  data () {
    return {
      keyword: '',
      foundUsers: undefined,
      isRequesting: false,
      currentPage: 1,
      totalPages: 1,
      bootstrapPaginationClasses: {
        ul: 'pagination',
        li: 'page-item',
        liActive: 'active',
        liDisable: 'disabled',
        button: 'page-link'
      },
      paginationAnchorTexts: {
        first: '&laquo;',
        prev: '&lsaquo;',
        next: '&rsaquo;',
        last: '&raquo;'
      },
      //   notShowPagination: false,
      isShowPagination: false
    }
  },
  computed: {
    ...mapGetters({
      users: 'users',
      pagination: 'pagination'
    }),
    displayUsers () {
      if (this.foundUsers !== undefined) {
        return this.foundUsers
      } else {
        this.isShowPagination = true
        // this.notShowPagination = false
        return this.users
      }
    }
  },
  components: {
    AppLoading: Loading,
    vPagination
  },
  watch: {
    pagination () {
      if (this.pagination !== undefined) {
        this.totalPages = Number(this.pagination.pageCount)
        this.currentPage = Number(this.pagination.currentPage)
      }
    },
    currentPage () {
      this.isRequesting = true
      const vm = this
      this.listUsers({
        page: this.currentPage,
        response (error) {
          vm.isRequesting = false
          if (error.code !== 0) {
            console.error('list fail')
          }
        }
      })
    }

  },
  methods: {
    ...mapActions({
      listUsers: 'listUsers',
      userSearchUsers: 'searchUsers'
    }),
    showUserDetail (objectId) {
      this.$router.push({ name: 'edituser', params: { objectId: objectId } })
    },
    setOrder (index) {
      return ((this.currentPage - 1) * 10) + (index + 1)
    },
    searchUsers () {
      if (this.keyword.trim() === '') {
        this.foundUsers = undefined
        this.keyword = ''
        return
      }

      this.isRequesting = true
      const vm = this
      this.userSearchUsers({
        keyword: this.keyword,
        response: users => {
          vm.isRequesting = false
          vm.foundUsers = new Array()
          vm.foundUsers = users
          this.isShowPagination = false
        }
      })
    //   this.notShowPagination = true
    },
    validateKeyword () {
      if (this.keyword === '') {
        this.foundUsers = undefined
      }
    },
    showPagination (dataPagination) {
      this.paginationPageTransaction = dataPagination
      if (dataPagination.pageCount == 0) {
        this.isShowPagination = false
      } else {
        this.isShowPagination = true
      }
    }
  },
  mounted () {
    // if (this.users.length === 0) {
    this.isRequesting = true
    const vm = this
    this.listUsers({
      page: 1,
      response (error) {
        vm.isRequesting = false
        if (error.code !== 0) {
          console.error('list fail')
        }
      }
    })
    // }
  }
}
</script>
<style scoped>
tr:hover {
  cursor: pointer;
  background-color: #eeeeee;
}
</style>
