import axios from 'axios'

const getAccessToken = async (idToken, callback) => {
  try {
    const result = await axios.post(
      process.env.VUE_APP_SERVICE_BASE_URL + '/user/login/google',
      {},
      {
        headers: {
          'access-token': idToken
        }
      }
    )
    const json = result.data
    if (json.error.code === 0) {
      callback({
        error: { code: 0, message: 'success' },
        data: json.data
      })
    } else {
      callback({
        error: { code: 1, message: 'get access token fail' },
        data: json.error
      })
    }
  } catch (error) {
    console.log(error)
  }
}

export { getAccessToken }
