<template>
<div class="card border-0">
  <div class="card-body">
    <nav aria-label="breadcrumb">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <router-link :to="{name:'listthings'}">รายการสิ่งของที่ให้ยืมได้</router-link>
        </li>
        <li class="breadcrumb-item active" aria-current="page">{{ pageTitle }}</li>
      </ol>
    </nav>
    <form v-if="isRequesting === false">
      <div v-if="errorMessage" class="alert alert-danger" role="alert">{{ errorMessage }}</div>
      <div v-if="successMessage" class="alert alert-success" role="alert">{{ successMessage }}</div>
      <div class="form-group">
        <label for="formGroupExampleInput">รหัส<add-star /></label>
        <input type="text" class="form-control" v-model="thing.code">
      </div>
      <!-- <div class="form-group">
        <label for="formGroupExampleInput">ชื่อเรียก<add-star /></label>
        <input type="text" class="form-control" v-model="thing.name" @focus="hideErrorMessage">
      </div> -->
      <div class="form-group">
        <label for="formGroupExampleInput">ประเภท<add-star /></label>
        <select class="form-control" v-model="thing.type">
            <option value="FOOD_BOX">กล่องข้าว</option>
            <option value="CLOTH_BAG">ถุงผ้า</option>
        </select>
      </div>
      <!-- <div class="form-group">
        <label for="formGroupExampleInput">รุ่น/รายละเอียด</label>
        <input type="text" class="form-control" v-model="thing.model">
      </div> -->
      <div class="form-group">
        <label for="formGroupExampleInput">คำอธิบายอื่นๆ</label>
        <input type="text" class="form-control" v-model="thing.description">
      </div>
    </form>
    <div v-if="isRequesting === false" class="row">
      <div class="col d-flex justify-content-between align-items-center">
        <button class="btn btn-primary" @click="save">บันทึก</button>
        <button v-if="thing.objectId !== ''" class="btn btn-danger" @click="deleteThing">ลบ</button>
      </div>
    </div>
    <app-loading v-if="isRequesting === true"></app-loading>
  </div>
</div>
</template>
<script>
import { mapActions } from 'vuex'
import Loading from '@/components/Loading'
import StarRed from '../../components/StarRed'
export default {
  data () {
    return {
      errorMessage: '',
      successMessage: '',
      thing: {
        objectId: '',
        code: '',
        // name: '',
        type: '',
        // model: '',
        description: ''
      },
      isRequesting: false
    }
  },
  computed: {
    pageTitle () {
      if (this.$route.name === 'editthing') {
        return 'แก้ไขสิ่งของ'
      } else {
        return 'สร้างสิ่งของ'
      }
    }
  },
  components: {
    AppLoading: Loading,
    AddStar: StarRed
  },
  methods: {
    ...mapActions({
      findThingByObjectId: 'findThingByObjectId',
      addThing: 'addThing',
      updateThing: 'updateThing',
      thingDeleteThing: 'deleteThing',
      clearThings: 'clearThings'
    }),
    hideErrorMessage () {
      this.errorMessage = ''
    },
    save () {
      this.successMessage = ''
      this.errorMessage = ''
      const vm = this

      if (this.thing.code === '' && this.thing.type === '') {
        this.errorMessage = 'กรุณาตั้งชื่อสิ่งของ'
        return
      }

      if (this.thing.objectId === '') {
        this.isRequesting = true
        this.addThing({
          thing: { code: this.thing.code, type: this.thing.type, description: this.thing.description },
          response (error) {
            vm.isRequesting = false
            if (error.code === 0) {
              vm.clearThings()
              vm.successMessage = 'Save user success'
              return
            }
            vm.errorMessage = 'Save user error'
          }
        })
      } else {
        const vm = this
        this.isRequesting = true
        this.updateThing({
          thing: vm.thing,
          response: error => {
            vm.isRequesting = false
            if (error.code === 0) {
              vm.clearThings()
              vm.successMessage = 'Update success'
              return
            }
            vm.errorMessage = 'Update fail'
          }
        })
      }
    },
    deleteThing () {
      const objectId = this.thing.objectId

      if (confirm('คุณต้องการที่จะลบ ?')) {
        if (objectId !== undefined && objectId !== '') {
          const vm = this
          this.isRequesting = true
          this.thingDeleteThing({
            objectId,
            response: res => {
              vm.isRequesting = false
              if (res.error.code === 0) {
                vm.clearThings()
                return this.$router.replace({ name: 'listthings' })
              }
              vm.errorMessage = 'Delete fail'
            }
          })
        }
      }
    },
    reload () {
      const objectId = this.$route.params.objectId
      if (objectId !== undefined && objectId !== '') {
        this.isRequesting = true
        const vm = this
        this.findThingByObjectId({
          objectId,
          response: res => {
            vm.isRequesting = false
            if (res.data === undefined) {
              return vm.$router.replace({ name: 'listthings' })
            }
            vm.thing = res.data
          }
        })
      }
    }
  },
  mounted () {
    this.reload()
  }
}
</script>
