<template>
  <div class="row">
    <div class="col-sm-12">
      <video playsinline ref="cameraVideo" v-show="isNotTakePicture" style="width: inherit" />
    </div>
    <div class="col-sm-12">
      <canvas id="cameraCanvas" ref="cameraCanvas" aria-hidden="true" />
    </div>
    <div class="col-sm-12 text-center" v-show="isNotTakePicture">
      <button @click="click"><i class="fas fa-camera"></i></button>
    </div>
    <div class="col-sm-12">
      <img ref="img" style="width:100;height:100;" />
    </div>
    <div class="col-sm-12 text-center" v-show="isNotTakePicture === false">
      <button class="m-2" @click="savePicture"><i class="fas fa-save"></i></button>
      <button class="m-2" @click="newTakePicture"><i class="fas fa-redo"></i></button>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  data: () => {
    return {
      img: 'Hello',
      isNotTakePicture: true,
      NumPicture: ''
    }
  },
  props: ['width', 'height', 'facingMode'],
  mounted () {
    let page = this.$route.params.numPicture
    this.$nextTick(()=>{
        this.updateFeed()
    })
    
  },
  computed: {
    everything () {
      return this.width, this.height, this.facingMode
    }
  },
  watch: {
    everything () {
      this.updateFeed()
    }
  },
  methods: {
    ...mapActions({
      //   addPicture: "addPicture"
      uploadImage: 'uploadImage'
    }),
    updateFeed () {
      let constraints = {
        audio: false,
        video: {
          facingMode: this.facingMode || 'user'
        }
      }
      if (this.width) this.$refs.cameraVideo.style.width = this.width + 'px'
      if (this.height) this.$refs.cameraVideo.style.height = this.height + 'px'
      this.$refs.cameraVideo.setAttribute('autoplay', '')
      this.$refs.cameraVideo.setAttribute('muted', '')
      this.$refs.cameraVideo.setAttribute('playsinline', '')
      console.log(constraints)
      navigator.mediaDevices.getUserMedia(constraints)
        .then(mediaStream => {
          this.$refs.cameraVideo.srcObject = mediaStream
          this.$refs.cameraVideo.addEventListener('loadedmetadata', () => {
            this.$refs.cameraVideo.play()
          })
        })
        .catch(error => {
          console.log('Error', error)
        })
    },
    click () {
      this.$refs.cameraCanvas.width = this.$refs.cameraVideo.offsetWidth
      this.$refs.cameraCanvas.height = this.$refs.cameraVideo.offsetHeight
      this.$refs.cameraCanvas
        .getContext('2d')
        .drawImage(
          this.$refs.cameraVideo,
          0,
          0,
          this.$refs.cameraCanvas.width,
          this.$refs.cameraCanvas.height
        )
      this.$refs.img.src = this.$refs.cameraCanvas.toDataURL('image/jpeg')
      this.NumPicture = this.$route.params.numPicture
      this.isNotTakePicture = false
    },
    newTakePicture () {
      this.isNotTakePicture = true
      this.$refs.img.src = ''
      this.NumPicture = ''
    },
    dataURLtoFile (dataurl, filename) {
      var arr = dataurl.split(',')
      var mime = arr[0].match(/:(.*?);/)[1]
      var bstr = atob(arr[1])
      var n = bstr.length
      var u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new File([u8arr], filename, { type: mime })
    },
    savePicture () {
      let vm = this
      //   this.addPicture({
      //     numPicture: this.$route.params.numPicture,
      //     picture: this.$refs.img.src
      //   });
      let imageBase64 = this.$refs.img.src
      let namePicture =
        new Date().getDay() +
        '' +
        new Date().getMonth() +
        '' +
        new Date().getFullYear() +
        '' +
        new Date().getTime()

      var file = this.dataURLtoFile(imageBase64, namePicture)
      let formData = new FormData()
      formData.append('file', file)
      this.uploadImage({
        file: formData,
        page: this.$route.params.numPicture,
        response: res => {
          if (res.error.code === 0) {
            vm.$router.push({ path: '/return' })
          } else {
            console.log(res.error.message)
          }
        }
      })
    }
  }
}
</script>
<style scoped>
#cameraCanvas {
  display: none;
}
</style>
