let Thing = require('../model/thing')

const state = {
  things: [],
  paginationThing: [],
  foundThingsList: []
}

const mutations = {
  SET_THINGS (state, things) {
    state.things = things
  },
  SET_PAGINATION_THING (state, page) {
    state.paginationThing = page
  },
  SET_FOUND_THINGS (state, foundThings) {
    state.foundThingsList = foundThings
  }
}

const actions = {
  clearThings ({ commit }, payload) {
    commit('SET_THINGS', [])
  },

  listThings ({ commit }, { page, type, response }) {
    Thing.list(page, type, res => {
      if (res.error.code === 0) {
        commit('SET_THINGS', res.data)
        commit('SET_PAGINATION_THING', res.pagination)
      }
      response({
        data: res.data,
        error: res.error
      })
    })
  },

  // searchThings({ state, commit }, { keyword, response }) {
  //   let foundThings = []
  //   for (let i = 0;i < state.things.length;i++) {
  //     const n = state.things[i].keyword.indexOf(keyword)
  //     if (n > -1) {
  //       foundThings.push(state.things[i])
  //       if (foundThings.length >= 5) { break }
  //     }
  //   }
  //   response(foundThings)
  // },
  searchThings ({ commit }, { keyword, response }) {
    Thing.searchThing(keyword, res => {
      if (res.error.code === 0) {
        commit('SET_FOUND_THINGS', res.data)
      }
      response({
        data: res.data,
        error: res.error
      })
    })
  },

  addThing ({ commit, rootState }, { thing, response }) {
    Thing.create(thing, res => {
      response(res.error)
    })
  },

  updateThing ({ commit }, { thing, response }) {
    Thing.update(thing, res => {
      response(res.error)
    })
  },

  findThingByObjectId ({ state, commit }, { objectId, response }) {
    Thing.findByObjectId(objectId, res => {
      response(res)
    })
  },

  deleteThing ({ commit }, { objectId, response }) {
    Thing.deleteThing(objectId, res => {
      response(res)
    })
  }
}

const getters = {
  things (state) {
    return state.things
  },
  paginationThing (state) {
    return state.paginationThing
  },
  foundThingsList (state) {
    return state.foundThingsList
  }
}

module.exports = {
  state,
  mutations,
  actions,
  getters
}
