import Vue from 'vue'
import Vuex from 'vuex'

import thing from '@/store/thing'
import transaction from '@/store/transaction'
import user from '@/store/user'
import image from '@/store/image'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    thing,
    transaction,
    user,
    image
  },
  actions: {
    initializeDatabase ({ rootState }, response) {
      this.dispatch('listUsers', {
        response: error => {
          if (error.code !== 0) {
            return response(error)
          }

          if (rootState.thing.things.length === 0) {
            this.dispatch('listThings', {
              response: error => {
                return response(error)
              }
            })
          }
        }
      })
    }
  }
})
