<template>
  <div class="card border-0">
    <div class="card-body">
      <h3>จัดการสิ่งของที่ให้ยืมได้</h3>
      <div class="alert alert-info" role="alert">
        <router-link
          :to="{ name: 'creatething' }"
          class="btn btn-primary"
          style="margin-right:20px"
        >เพิ่มสิ่งของ</router-link>
        <span>
          <i class="fas fa-info-circle"></i> สิ่งของที่มีให้ยืม
        </span>
      </div>
      <form @submit.prevent="searchThings" class="mt-4">
        <div class="row">
          <div class="col d-flex justify-content-between align-items-center">

        <select class="form-control mr-3 w-25" v-model="selectedType">
          <option value="All">ทั้งหมด</option>
          <option value="FOOD_BOX">กล่องข้าว</option>
          <option value="CLOTH_BAG">ถุงผ้า</option>
        </select>

            <input
              v-model="keyword"
              type="text"
              class="form-control"
              placeholder="ค้นหาสิ่งของจากชื่อ หรือรุ่น"
              style="margin-right: 20px"
              @keyup="validateKeyword"
            />
            <button class="btn btn-primary">ค้นหา</button>
          </div>
        </div>
      </form>
      <table v-if="isRequesting === false" class="table mt-1 table-responsive-lg">
        <thead class="thead-dark">
          <tr>
            <th scope="col">ลำดับ</th>
            <th scope="col">รหัสของ</th>
            <th scope="col">ประเภท</th>
            <th scope="col">คำอธิบายอื่นๆ</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(thing, index) in displayThings"
            :key="thing.objectId"
            @click="showThingDetail(thing.objectId)"
          >
            <th scope="row">{{ setOrder(index,currentPage) }}</th>
            <td>{{ thing.code }}</td>
            <td>{{ thing.typeName }}</td>
            <td>{{ thing.description }}</td>
          </tr>
        </tbody>
      </table>
      <app-loading v-if="isRequesting === true"></app-loading>
      <v-pagination
        v-show="isShowPagination"
        class="float-right"
        v-model="currentPage"
        :page-count="totalPages"
        :classes="bootstrapPaginationClasses "
        :labels="paginationAnchorTexts"
      ></v-pagination>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import Loading from '@/components/Loading'
import vPagination from 'vue-plain-pagination'
export default {
  data () {
    return {
      keyword: '',
      foundThings: undefined,
      isRequesting: false,
      currentPage: 1,
      totalPages: 1,
      bootstrapPaginationClasses: {
        ul: 'pagination',
        li: 'page-item',
        liActive: 'active',
        liDisable: 'disabled',
        button: 'page-link'
      },
      paginationAnchorTexts: {
        first: '&laquo;',
        prev: '&lsaquo;',
        next: '&rsaquo;',
        last: '&raquo;'
      },
      isShowPagination: false,
      selectedType: 'All'
    }
  },
  computed: {
    ...mapGetters({
      things: 'things',
      paginationThing: 'paginationThing',
      foundThingsList: 'foundThingsList'
    }),
    displayThings () {
      if (this.foundThings !== undefined) {
        return this.foundThings
      }
      return this.things
    }
  },
  watch: {
    paginationThing () {
      if (this.paginationThing !== undefined) {
        this.totalPages = Number(this.paginationThing.pageCount)
        this.currentPage = Number(this.paginationThing.currentPage)
        this.showPagination(this.currentPage)
      }
    },
    currentPage () {
      const vm = this
      this.listThings({
        page: this.currentPage,
        response (error) {
          vm.isRequesting = false
          if (error.code !== 0) {
            console.log('list fail')
          }
        }
      })
    },
    selectedType () {
      let vm = this
      if (this.selectedType === 'All') {
        this.listThings({
          page: 1,
          response (error) {
            vm.isRequesting = false
            if (error.code !== 0) {
              console.log('list fail')
            }
          }
        })
      } else if (this.selectedType === 'FOOD_BOX') {
        this.listThings({
          page: 1,
          type: this.selectedType,
          response (error) {
            vm.isRequesting = false
            if (error.code !== 0) {
              console.log('list fail')
            }
          }
        })
      } else {
        this.listThings({
          page: 1,
          type: this.selectedType,
          response (error) {
            vm.isRequesting = false
            if (error.code !== 0) {
              console.log('list fail')
            }
          }
        })
      }
    }
  },
  methods: {
    ...mapActions({
      listThings: 'listThings',
      thingSearchThings: 'searchThings'
    }),
    setOrder (index) {
      return (this.currentPage - 1) * 10 + (index + 1)
    },
    showThingDetail (objectId) {
      this.$router.push({ name: 'editthing', params: { objectId: objectId } })
    },
    searchThings () {
      if (this.keyword.trim() === '') {
        this.foundThings = undefined
        this.keyword = ''
        return
      }
      const vm = this
      this.isRequesting = true
      this.thingSearchThings({
        keyword: this.keyword,
        response (res) {
          vm.isRequesting = false
          if (res.error.code !== 0) {
            console.log('search fail')
          } else {
            vm.foundThings = vm.foundThingsList
            vm.isShowPagination = false
          }
        }
        // response: things => {
        //   vm.isRequesting = false
        //   vm.foundThings = new Array()
        //   vm.foundThings = things
        // }
      })
    },
    validateKeyword () {
      if (this.keyword === '') {
        this.foundThings = undefined
        this.isShowPagination = true
      }
    },
    showPagination (dataPagination) {
      this.paginationPageTransaction = dataPagination
      if (dataPagination.pageCount == 0) {
        this.isShowPagination = false
      } else {
        this.isShowPagination = true
      }
    }
  },
  mounted () {
    // if (this.things.length === 0) {
    this.isRequesting = true
    const vm = this
    this.listThings({
      page: 1,
      response (error) {
        vm.isRequesting = false
        if (error.code !== 0) {
          console.log('list fail')
        }
      }
    })
    // } else {
    //     console.log(this.paginationThing.pageCount)
    //     if(this.paginationThing.cou)
    //   this.totalPages = Number(this.paginationThing.pageCount)
    //   this.currentPage = Number(this.paginationThing.currentPage)
    // }
  },
  components: {
    AppLoading: Loading,
    vPagination
  }
}
</script>
<style scoped>
tr:hover {
  cursor: pointer;
  background-color: #eeeeee;
}
</style>
