import Vue from 'vue'
import Router from 'vue-router'
// import firebase from 'firebase'

import Borrow from './views/Borrow'
import Login from './views/Login'

import ListUser from './views/user/List'
import EditUser from './views/user/Edit'

import ListThing from './views/thing/List'
import EditThing from './views/thing/Edit'

import Transaction from './views/transaction/List'
import Return from './views/transaction/Return'
import TakePicture from './views/image/TakePicture'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'borrow',
      component: Borrow
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: { requireAuth: false, shouldHideNavBar: true }
    },
    {
      path: '/users',
      name: 'listusers',
      component: ListUser
    },
    {
      path: '/users/:objectId',
      name: 'edituser',
      component: EditUser
    },
    {
      path: '/users/create',
      name: 'createuser',
      component: EditUser
    },
    {
      path: '/things',
      name: 'listthings',
      component: ListThing
    },
    {
      path: '/things/create',
      name: 'creatething',
      component: EditThing
    },
    {
      path: '/things/:objectId',
      name: 'editthing',
      component: EditThing
    },
    {
      path: '/transactions/all',
      name: 'listtransactions',
      component: Transaction,
      meta: { filter: 'all' }
    },
    {
      path: '/transactions/no-return',
      name: 'listNoReturnTransactions',
      component: Transaction,
      meta: { filter: 'onlyNoReturn' }
    },
    {
      path: '/transactions/my',
      name: 'myTransactions',
      component: Transaction,
      meta: { filter: 'onlyMe' }
    },
    {
      path: '/return',
      name: 'return',
      component: Return
    },
    {
      path: '/takepicture/:numPicture',
      name: 'takepicture',
      component: TakePicture
    }
  ]
})

router.beforeEach((to, from, next) => {
  const accessToken = window.localStorage.getItem('access-token')
  if (to.meta.requireAuth === undefined && accessToken === null) {
    return next({ name: 'login' })
  }
  next()
})

export default router
