<template>
  <div class="card border-0">
    <div class="card-body">
      <h2 v-if="filter === 'all'">รายการยืมคืนทั้งหมด</h2>
      <button v-if="filter === 'all'" class="btn btn-info mb-2 float-right" @click="download">
        <i class="fas fa-download mr-2"></i>Excel
      </button>
      <h2 v-if="filter === 'onlyNoReturn'">รายการที่ยังไม่ได้คืน</h2>
      <h2 v-if="filter === 'onlyMe'">รายการยืมของฉัน</h2>
      <div id="no-more-tables" class="mt-5">
        <table class="col-md-12 table-bordered table-striped table-condensed cf">
          <thead class="thead-dark">
            <tr>
              <th>
                <div style="padding:5px">หมายเลขการยืม</div>
              </th>
              <th>
                <div style="padding:5px">ชื่อ-สกุล ผู้ยืม</div>
              </th>
              <th>
                <div style="padding:5px">รายละเอียด</div>
              </th>
              <th>
                <div style="padding:5px">วันที่ยืม</div>
              </th>
              <th>
                <div style="padding:5px">กำหนดคืน</div>
              </th>
              <th>
                <div style="padding:5px">วันที่คืน</div>
              </th>
              <th>
                <div style="padding:5px">สถานะ</div>
              </th>
            </tr>
          </thead>
          <tbody v-if="transactions && transactions.length>0">
            <tr v-for="(transaction, index) in transactions" :key="index">
              <td data-title="หมายเลขการยืม">
                <div class="pl-3 pr-3">{{transaction.transactionNo}}</div>
              </td>
              <td
                v-if="transaction.borrower === undefined"
                data-title="ชื่อ-สกุล ผู้ยืม"
                class="text-color"
              >
                <div class="pl-3 pr-3">ไม่ระบุชื่อ-นามสกุล</div>
              </td>
              <td v-else data-title="ชื่อ-สกุล ผู้ยืม">
                <div class="pl-3 pr-3">
                  {{ transaction.borrower.firstname }}
                  {{ transaction.borrower.lastname }}
                </div>
              </td>
              <td data-title="รายละเอียด">
                <ol>
                  <li v-for="(thing, index) in transaction.borrowingThings" :key="index">
                    <div class="pl-1 pr-3">{{ thing.typeName }}({{ thing.code }})</div>
                  </li>
                </ol>
              </td>
              <td data-title="วันที่ยืม">
                <div
                  class="pl-3 pr-3"
                >{{ new Date(transaction.createdAt) | moment("DD MMM YYYY, HH:mm:ss") }}</div>
              </td>
              <td data-title="กำหนดคืน">
                <div
                  class="pl-3 pr-3"
                >{{ new Date(transaction.dueDate) | moment("DD MMM YYYY, HH:mm:ss") }}</div>
              </td>
              <td data-title="วันที่คืน" v-if="transaction.returnAt ">
                <div
                  class="pl-3 pr-3"
                >{{new Date(transaction.returnAt) | moment("DD MMM YYYY, HH:mm:ss")}}</div>
              </td>
              <td data-title="วันที่คืน" v-else style="text-align:center">
                <div class="pl-3 pr-3">-</div>
              </td>
              <td data-title="สถานะ" :class="isNoReturnTransaction(transaction) ? 'redText': ''">
                <div
                  class="pl-3 pr-3"
                >{{ describeReturnStatus(transaction.returnAt,transaction.dueDate) }}</div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <app-loading v-if="isRequesting"></app-loading>
    </div>
    <v-pagination
        v-show="isShowPagination"
        style="justify-content: flex-end"
        v-model="currentPage"
        :page-count="totalPages"
        :classes="bootstrapPaginationClasses "
        :labels="paginationAnchorTexts"
      ></v-pagination>
  </div>
</template>
<script>
import { mapActions } from 'vuex'
import Loading from '@/components/Loading'
import vPagination from 'vue-plain-pagination'

export default {
  data () {
    return {
      transactions: [],
      paginationPageTransaction: [],
      isRequesting: false,
      currentPage: 1,
      totalPages: 1,
      bootstrapPaginationClasses: {
        ul: 'pagination',
        li: 'page-item',
        liActive: 'active',
        liDisable: 'disabled',
        button: 'page-link'
      },
      paginationAnchorTexts: {
        first: '&laquo;',
        prev: '&lsaquo;',
        next: '&rsaquo;',
        last: '&raquo;'
      },
      isShowPagination: false,
      date: null
    }
  },
  computed: {
    filter () {
      return this.$route.meta.filter
    }
  },
  components: {
    AppLoading: Loading,
    vPagination
  },
  watch: {
    $route () {
      this.reloadData(1)
    },
    paginationPageTransaction () {
      if (this.paginationPageTransaction !== undefined) {
        this.currentPage = Number(this.paginationPageTransaction.currentPage)
        this.totalPages = Number(this.paginationPageTransaction.pageCount)
      }
    },
    currentPage () {
      this.reloadData(this.currentPage)
    }
  },
  methods: {
    ...mapActions({
      listTransactions: 'listTransactions',
      listOnlyNoReturnTransactions: 'listOnlyNoReturnTransactions',
      listOnlyMyTransactions: 'listOnlyMyTransactions',
      downloadTransaction: 'downloadTransaction'
    }),
    download () {
      this.downloadTransaction({
        response: res => {
        //   const url = window.URL.createObjectURL(new Blob([res]));
        //   console.log(url)
        //   const link = document.createElement("a");
        //   link.href = url;
        //   link.setAttribute("download", "report.xlsm"); // or any other extension
        //   document.body.appendChild(link);
        //   link.click();
        }
      })
    },
    setOrder (index) {
      return (this.currentPage - 1) * 10 + (index + 1)
    },
    isNoReturnTransaction (transaction) {
      if (transaction.returnAt === undefined) {
        return true
      }
    },
    describeReturnStatus (status, dueDate) {
      let overDueDate =
        status !== undefined
          ? Math.round(
            Math.abs(
              new Date(status).getTime() - new Date(dueDate).getTime()
            ) /
                (24 * 60 * 60 * 1000)
          )
          : Math.round(
            Math.abs(new Date().getTime() - new Date(dueDate).getTime()) /
                (24 * 60 * 60 * 1000)
          )
      if (status === undefined) {
        return overDueDate > 1
          ? 'ยังไม่ได้คืน [เกินกำหนด ' + overDueDate + ' วัน]'
          : 'ยังไม่คืน'
      } else {
        return overDueDate > 1
          ? 'คืนแล้ว [เกินกำหนด ' + overDueDate + ' วัน]'
          : 'คืนแล้ว'
      }
      //   if (status === 'normal') {
      //     return 'ได้รับคืนตามปกติ'
      //   } else if (status === 'lost') {
      //     return 'มีบางชิ้นสูญหาย'
      //   } else if (status === 'broken') {
      //     return 'มีบางชิ้นชำรุด'
      //   } else {
      //     return 'ยังไม่ได้รับคืน'
      //   }
    },
    reloadData (page) {
      this.isRequesting = true
      this.transactions = []
      if (this.filter === 'all') {
        this.listTransactions({
          page: page,
          response: res => {
            this.isRequesting = false
            this.transactions = res.data
            this.ShowPagination(res.pagination)
          }
        })
      } else if (this.filter === 'onlyNoReturn') {
        this.listOnlyNoReturnTransactions({
          page: page,
          response: res => {
            this.isRequesting = false
            this.transactions = res.data
            this.ShowPagination(res.pagination)
          }
        })
      } else {
        this.listOnlyMyTransactions({
          page: page,
          response: res => {
            this.isRequesting = false
            this.transactions = res.data
            this.ShowPagination(res.pagination)
          }
        })
      }
    },
    ShowPagination (dataPagination) {
      this.paginationPageTransaction = dataPagination
      if (dataPagination.pageCount == 0) {
        this.isShowPagination = false
      } else {
        this.isShowPagination = true
      }
    }
  },
  mounted () {
    this.reloadData(1)
  }
}
</script>
<style scoped>
.redText {
  color: red;
}
</style>
